<script>
export default {
  name: "Main",
  components: {},
  data() {
    return {
      items: [],
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("rest/admin_users");
      this.items = [...res.data].splice(0)[0];
    },
    logout(){
      localStorage.removeItem('token')
      this.$router.push('/login')
    }
  },
  created() {
    this.fetch();
  },
};
